import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MdButtonModule, MdCheckboxModule, MdInputModule, MdToolbarModule, MdSidenavModule } from '@angular/material';
import { NgBinderModule } from '../ng-binder/ng-binder.module';
import { NodeManagerModule } from '../node-manager/node-manager.module';
import { TangerineFormsModule } from '../tangerine-forms/tangerine-forms.module';


import { PagesRoutingModule } from './pages-routing.module';
import { DemoComponent } from './demo/demo.component';
import { MyTangyFormComponent } from './my-tangy-form/my-tangy-form.component';
import { IndexComponent } from './index/index.component';

@NgModule({
  imports: [ BrowserModule ,  FormsModule ,  HttpModule ,  BrowserAnimationsModule ,  MdButtonModule, MdCheckboxModule, MdInputModule, MdToolbarModule, MdSidenavModule ,  NgBinderModule ,  NodeManagerModule ,  TangerineFormsModule , // [

    PagesRoutingModule
  ],
  declarations: [DemoComponent, MyTangyFormComponent, IndexComponent]
})
export class PagesModule { }



